.vector__calculator {
    background-color: #000000;
    height: 0.5px;
    width: 536px;
    margin: auto;
    margin-top: 36px;
}
.calculator__title {
    display: flex;
    justify-content: center;
    font-family: "Montserrat Alternates", solid;
    font-weight: 400;
    margin-top: 36px;
    margin-bottom: 49px;
}

.calculator__container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    margin-bottom: 36px;
}

.input__calculator {
    outline: 0;
    border: 0;
    width: 150px;
    height: 35px;
    padding: 10px, 20px, 10px, 20px;
    background-color: #D7D7D7;
    padding-left: 14.63px;
    box-shadow: 3px 3px 0px 0px #00000040;
    border-radius: 20px;
    font-family: "Montserrat Alternates", solid;
    font-weight: 400;
}

.button__calculator {
    align-items: center;
    width: 27px;
    height: 25px;
    border-radius: 20px;
    padding: 5px, 10px, 5px, 10px;
    background-color: black;
    color: #D7D7D7;
    box-shadow: 3px 3px 0px 0px #00000040; 
}

.ebutton__calculator {
    margin-left: 38px;
    margin-right: 38px;
    align-items: center;
    width: 27px;
    height: 25px;
    border-radius: 20px;
    padding: 5px, 10px, 5px, 10px;
    background-color: black;
    color: #D7D7D7;
    box-shadow: 3px 3px 0px 0px #00000040; 
}

.button__list {
    display: flex;
    list-style: none;
    width: 72px;
    height: 60px;
    border-radius: 20px;
    flex-wrap: wrap;
    margin-right: 38px;
    gap: 10px;
    list-style: none;
}


.calc__result {
    outline: 0;
    border: 0;
    width: 150px;
    height: 35px;
    padding: 10px, 20px, 10px, 20px;
    background-color: #D7D7D7;
    padding-left: 14.63px;
    box-shadow: 3px 3px 0px 0px #00000040;
    border-radius: 20px;
    font-family: Montserrat Alternates;
    font-size: 12px;
    font-weight: 400;
    line-height: 15px;
    letter-spacing: 0em;
    text-align: left;
    }
